body,
html,
.App {
  height: 100%;
  width: 100%; }

.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap; }

#map {
  top: 140px;
  right: 0;
  width: 100%;
  height: calc(100% - 78px); }

/* ====================== Horizontal Views ====================== */
@media screen and (min-width: 560px) {
  #map {
    top: 0;
    width: 65%;
    height: 100%; } }



