@import url("https://fonts.googleapis.com/css?family=Quicksand");
.gm-style-iw {
  width: 300px !important;
  height: 300px;
  border-style: solid;
  border-color: #F78888;
  border-radius: 10px;
  background-color: #ECECEC;
  -webkit-box-shadow: 0 10px 6px -6px #777777;
  box-shadow: 0 10px 6px -6px #777777; }

#infoWindow {
  height: 300px;
  border-radius: 10px; }
  #infoWindow h3 {
    margin: 0 5px 5px 10px; }

#photo {
  width: 300px;
  height: 150px;
  -o-object-fit: cover;
  object-fit: cover;
  border-radius: 8px 8px 0 0;
  display: block; }

#title {
  background-color: #F3D250;
  font: bold 6vw "Quicksand", sans-serif;
  text-align: center; }

#rating {
  background-color: red;
  padding: 2px 10px;
  margin-bottom: 5px; }
  #rating span {
    color: #F3D250;
    font: italic bold 16px "Quicksand", sans-serif; }

#info {
  margin: 0 10px; }
  #info h3 {
    margin: 0; }

#getDirection {
  margin: 5px;
  border-radius: 5px;
  background-color: #F3D250;
  font: bold 16px "Quicksand", sans-serif; }

#pano {
  width: 300px;
  height: 100px;
  border-radius: 0 0 8px 8px; }

/* ====================== Horizontal Views ====================== */
@media screen and (min-width: 560px) {
  .gm-style-iw {
    width: 316px !important; }
  #pano {
    border-radius: 0; }
  #title {
    font-size: 24px; } }



