@import url("https://fonts.googleapis.com/css?family=Quicksand");
.desktop__headers {
  display: none; }

.panel {
  width: 100%;
  background-color: #ECECEC;
  text-align: center;
  height: 140px; }

#forms {
  display: -ms-flexbox;
  display: flex; }

form {
  width: 50%; }

#searchRestaurant {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  height: 40px;
  width: 80%;
  font: bold 16px "Quicksand", sans-serif;
  padding-left: 15px; }

.mobileButton {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  height: 40px;
  width: 20%;
  font: bold 16px "Quicksand", sans-serif;
  padding-left: 15px;
  padding-left: 5px; }

#myLocation {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  height: 40px;
  width: 80%;
  font: bold 16px "Quicksand", sans-serif;
  padding-left: 15px; }

#max-duration, #transit {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  height: 40px;
  width: 35%;
  font: bold 16px "Quicksand", sans-serif;
  padding-left: 15px;
  float: left; }

#selectRating {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  height: 40px;
  width: 30%;
  font: bold 16px "Quicksand", sans-serif;
  padding-left: 15px;
  float: left; }

#openNow {
  padding-left: 0;
  font-size: 12px;
  margin: 10px 0; }

.desktopButtonTitle {
  display: none; }

.title {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  font-family: "Quicksand", sans-serif;
  width: 100%;
  background-color: #F78888;
  color: white;
  margin: 0 auto;
  padding: 10px 50px;
  font-size: 32px; }

/* ====================== Horizontal Views ====================== */
@media screen and (min-width: 560px) {
  .desktopButtonTitle {
    display: block; }
  .mobileButtonTitle {
    display: none; }
  .desktop__headers {
    text-align: left;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    padding-left: 5%;
    display: block;
    width: 100%;
    font: 900 24px "Quicksand", sans-serif;
    margin-bottom: 10px; }
  .panel {
    width: 35%;
    height: 100vh; }
  .title {
    font-size: 3vw;
    margin-bottom: 40px; }
  #myLocation {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    height: 65px;
    width: 60%;
    border-style: solid;
    border-color: #F78888;
    margin: 0 auto 20px;
    font: bold 24px "Quicksand", sans-serif;
    padding-left: 15px;
    border-radius: 15px;
    border-width: 5px;
    float: left;
    margin-left: 5%;
    margin-bottom: 40px; }
  #locationSubmit {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    height: 50px;
    width: 25%;
    color: white;
    font: bold 24px "Quicksand", sans-serif;
    float: right;
    border-radius: 50%;
    background-color: #F78888;
    -webkit-box-shadow: 0 10px 6px -6px #777777;
    box-shadow: 0 10px 6px -6px #777777;
    text-shadow: 1px 2px #777777;
    margin: 0 5% 20px auto;
    margin-bottom: 60px; }
  #searchSubmit {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    height: 50px;
    width: 25%;
    color: white;
    font: bold 24px "Quicksand", sans-serif;
    float: right;
    border-radius: 50%;
    background-color: #F3D250;
    -webkit-box-shadow: 0 10px 6px -6px #777777;
    box-shadow: 0 10px 6px -6px #777777;
    text-shadow: 1px 2px #777777;
    margin: 0 5% 20px auto;
    margin-bottom: 60px; }
  #searchRestaurant {
    float: left;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    height: 65px;
    width: 60%;
    border-style: solid;
    border-color: #F3D250;
    margin: 0 auto 20px;
    font: bold 24px "Quicksand", sans-serif;
    padding-left: 15px;
    border-radius: 15px;
    border-width: 5px;
    margin-left: 5%;
    margin-bottom: 40px; }
  #selectRating {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    height: 65px;
    width: 25%;
    border-style: solid;
    border-color: #90CCF4;
    margin: 0 auto 20px;
    font: bold 24px "Quicksand", sans-serif;
    padding-left: 15px;
    border-radius: 15px;
    border-width: 5px;
    margin: 0 70% 40px 5%; }
  #transit, #max-duration {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    height: 65px;
    width: 35%;
    border-style: solid;
    border-color: #5DA2D5;
    margin: 0 auto 20px;
    font: bold 24px "Quicksand", sans-serif;
    padding-left: 15px;
    border-radius: 15px;
    border-width: 5px;
    margin-left: 5%;
    margin-right: 5%; }
  #forms {
    display: block; }
  form {
    width: 100%; } }



